const url = "api/posts/"

const getPosts = async ()=>{
    try{
        const posts = await fetch(url)
        const res = await posts.json()
        return res.map(post=>({
            ...post
        }))
    }catch(err){
        console.error(err)
    }
    
}


const insertPosts = async (title, body)=>{
    const doc = {
        title,
        body
    }

    await fetch(url,{
        method:"POST",
        headers:{"Content-Type":"application/json"},
        body:JSON.stringify(doc)
    }).then(()=>{return}).catch((err)=>console.error(err))
}


const deletePost = async(id)=>{
    await fetch(url + id, {
        method:"DELETE"
    }).then(()=>{return}).catch((err)=>console.error(err))
}

const postService = {
    getPosts,
    insertPosts,
    deletePost
}

export default postService