<template>
  <div class="hello">
    <h1>Welcome to Your Posts</h1>
    <p v-if="error" class="error">{{this.error}}</p>
    <input v-model="this.title" placeholder="Enter Post Title"/><br/>
    <input v-model="this.body" placeholder="Enter Post Body"/><br/>
    <button v-on:click="addPost">Add Post</button><hr/>
    <div class="post-container">
      <div class="post" v-for="(post) in this.posts" v-bind:key="post._id">
        <h1>{{post.title}}</h1>
        <p>{{post.body}}</p>
        <button v-on:click="removePost(post._id)">Delete</button>
      </div>
    </div>
  </div>
</template>

<script>
import postService from "../postService"
export default {
  name: 'PostCom',
  data(){
    return{
      posts:[],
      error:"",
      title:"",
      body:""
    }
  },
  async created(){
    try{
      this.posts = await postService.getPosts()
      //console.log(this.posts)
    }catch(err){
      this.error = err.message
      //console.log(this.error)
    }
    
  },
  computed:{
    text1(){
      return this.text
    }
  },
  methods:{
    async addPost(){
      await postService.insertPosts(this.title, this.body)
      this.title=""
      this.body=""
      window.location.reload()
    },
    async removePost(postID){
      await postService.deletePost(postID)
      window.location.reload()
    }
  }
}
</script>

